
    import {defineComponent, computed} from 'vue';

    export default defineComponent({
        name: 'Button',
        props: {
            text: {
                type: String,
                default: '',
            },
            colorBlue: {
                type: Boolean,
                default: false,
            },
            colorWhite: {
                type: Boolean,
                default: false,
            },
        },
        setup(props) {
            const buttonModifiers = computed<(false | string)[]>(() => ([
                props.colorBlue && 'button--color-blue',
                props.colorWhite && 'button--color-white',
            ]));

            return {
                buttonModifiers,
            };
        },
    });
